



import React from 'react';
import { Row, Col, Card, Table, Tabs, Tab, Badge, Button, Modal } from 'react-bootstrap';

import Loader from '../../App/layout/Loader/index'

import { getVendor, deleteVendors } from '../../api/inventory/vendorApi';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import { getAccount, deleteAccounts, closeAccount, getTransactions } from '../../api/accounting/account';
import { getLedgerEntries } from '../../api/accounting/ledger';
import { getStartEnd } from '../../helpers/getStartEnd';
import { NavLink } from 'react-router-dom';
import Num from '../../App/components/Reusables/Num';




const Account = (props) => {
    //get the query string to know which txn to focus
    const queryString = props.location.search;
    const queryParams = new URLSearchParams(queryString);
    const toFocus = parseInt(queryParams.get('focus'));

    const [loading, setLoading] = React.useState(true);
    const [accountData, setAccountData] = React.useState({});
    const [deleteData, setDeleteData] = React.useState({ showModal: false, accountId: null, accountUuid: null })
    const [closeData, setCloseData] = React.useState({ showModal: false });
    const [entries, setEntries] = React.useState({})

    React.useEffect(() => {

        const controller = new AbortController();

        (async () => {
            let ladder = 0;


            try {
                const accountRes = await getAccount({ action: 'get', account_uuid: props.match.params.id }, controller.signal)
                console.log(accountRes)
                if (!accountRes.json.status) {
                    Jhilikka.fire({ icon: 'error', title: accountRes.json.error, background: '#ffe5de' })
                    return;
                }
                const account = accountRes.json.accounts[0];
                setAccountData({
                    id: account.id,
                    uuid: account.uuid,
                    type: account.account_type,
                    name: account.name,
                    isClosed: account.is_closed,
                    typeName: account.account_type_str,
                    openingBalance: account.opening_balance,
                    openingDate: account.opening_date,
                    hasParent: account.parent ? true : false,
                    due: account.due,
                    credit: account.credit,
                    balance: account.current_amount,
                    parentId: account.parent,
                    parentName: account.parent_str,
                    parentUuid: account.parent_uuid,
                    is_active: account.is_active,
                    customer: accountRes.json.customer || null,
                    vendor: accountRes.json.vendor || null,
                });
                ladder++;

                const txJson = {
                    "action": "get",
                    "account_id": account.id,
                    ...getStartEnd(props),
                }
                console.log(JSON.stringify(txJson))
                const txRes = await getTransactions(txJson)
                console.log(txRes);
                if (!txRes.json.status) {
                    Jhilikka.fire({ icon: 'error', title: txRes.json.error, background: '#ffe5de' })
                    return;
                }
                const bundles = txRes.json.entries;
                const tempEntries = { drSide: [], crSide: [] }
                for (let i in bundles) {
                    const bundle = bundles[i];
                    const cashEntry = bundle.find(en => en.account === account.id);
                    const cashAmount = cashEntry.payment[0].amount;
                    const cashEntrySide = cashEntry.is_add ? 'drSide' : 'crSide';

                    let tempAmount = 0;
                    for (let entry of bundle) {
                        const entryAmount = entry.payment[0].amount;
                        const entrySide = entry.is_add ? 'drSide' : 'crSide';
                        if (entrySide !== cashEntrySide) {
                            let oldTempAmount = tempAmount;
                            tempAmount += entryAmount;

                            const entryData = {
                                accountName: entry.account_str,
                                accountId: entry.account,
                                accountUuid: entry.account_uuid,
                                entryId: entry.id,
                                bundle: entry.bundle_id,
                                date: entry.date,
                                remarks: entry.remarks,
                                invoiceId: entry.payment[0].invoice,
                                purchaseOrderId: entry.payment[0].purchase_order,
                            }

                            if (tempAmount >= cashAmount) {
                                tempEntries[cashEntrySide].push({
                                    amount: cashAmount - oldTempAmount,
                                    ...entryData
                                })
                                break;
                            } else {
                                tempEntries[cashEntrySide].push({
                                    amount: entryAmount,
                                    ...entryData,
                                })
                            }
                        }
                    }
                }
                await setEntries({ ...tempEntries });
                ladder++

            } catch (err) {
                console.log(err)
            }
            if (ladder === 2) {
                setLoading(false)
            }

        })()

        return () => {
            controller.abort();
        }
    }, [])

    console.log(entries);

    const _triggerCloseModal = () => {
        setCloseData({ showModal: true });
    }
    const _triggerDeleteModal = (id, uuid) => {
        console.log(uuid);
        setDeleteData({ showModal: true, accountId: id, accountUuid: uuid });
    }


    const _closeAccount = () => {
        const requestJson = {
            "action": accountData.isClosed ? "open" : "close",
            "account_uuid": accountData.uuid,
            "account_id": accountData.id,
        }

        console.log(requestJson);

        closeAccount(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setAccountData({ ...accountData, isClosed: !accountData.isClosed });
                    setCloseData({ ...closeData, showModal: false })
                    Jhilikka.fire({ icon: 'success', title: `Account ${accountData.isClosed ? 're-opened' : 'closed'} successfully!`, background: '#dffff0' })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err)
            });
    }

    const _delete = (idToBeDeleted, uuidToBeDeleted) => {
        console.log("here we have", idToBeDeleted, uuidToBeDeleted)
        deleteAccounts({ action: 'delete', accounts_ids: [idToBeDeleted], accounts_uuids: [uuidToBeDeleted] })
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Account deleted successfully!', background: '#dffff0' })
                    props.history.push('/accounts');

                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
                setDeleteData({ ...deleteData, showModal: false, accountId: null, accountUuid: null })
            }).catch((err) => {
                console.log(err);
            });
    }

    if (loading) return <Loader />
    const longerSide = entries.drSide.length > entries.crSide.length ? 'drSide' : 'crSide';
    let crTotal = 0;
    let drTotal = 0;
    const EntriesList = entries[longerSide].map((en, index) => {
        const drEntry = entries.drSide[index] || {}
        const crEntry = entries.crSide[index] || {}
        console.log(en)
        console.log(drTotal)
        console.log(crTotal)
        crTotal += crEntry.amount || 0
        drTotal += drEntry.amount || 0

        let DrLink = drEntry.amount;
        let CrLink = crEntry.amount;
        if (drEntry.purchaseOrderId) DrLink = <NavLink to={`/purchase-order/${drEntry.purchaseOrderId}`} title={`Go to purchase order`}>{DrLink}</NavLink>
        if (crEntry.purchaseOrderId) CrLink = <NavLink to={`/purchase-order/${crEntry.purchaseOrderId}`} title={`Go to purchase order`}>{CrLink}</NavLink>
        if (drEntry.invoiceId) DrLink = <NavLink to={`/invoice/${drEntry.invoiceId}`} title={`Go to invoice`}>{DrLink}</NavLink>
        if (crEntry.invoiceId) CrLink = <NavLink to={`/invoice/${crEntry.invoiceId}`} title={`Go to invoice`}>{CrLink}</NavLink>


        const DrAccountName = drEntry.accountId ?
            <>By < NavLink title={`Go to ${drEntry.accountName} account`} to={'/account/' + drEntry.accountUuid} > {drEntry.accountName}</NavLink></> :
            null

        const CrAccountName = crEntry.accountId ?
            <>To < NavLink title={`Go to ${crEntry.accountName} account`} to={'/account/' + crEntry.accountUuid} > {crEntry.accountName}</NavLink> </> :
            null



        return <tr key={index}>
            <td className={drEntry.entryId === toFocus ? 'focusedTransaction' : null} >{drEntry.date}</td>
            <td className={drEntry.entryId === toFocus ? 'focusedTransaction' : null} >
                <div>{DrAccountName}</div>
                <div style={{ fontSize: '0.9em' }}>{drEntry.remarks ? `(${drEntry.remarks})` : ''}</div>
            </td>
            <td className={drEntry.entryId === toFocus ? 'focusedTransaction' : null} >{DrLink}</td>
            {/* ----------------divider----------------------------------------------------------------------- */}
            <td className={crEntry.entryId === toFocus ? 'focusedTransaction' : null} >{crEntry.date}</td>
            <td className={crEntry.entryId === toFocus ? 'focusedTransaction' : null} >
                <div>{CrAccountName}</div>
                <div style={{ fontSize: '0.9em' }}>{crEntry.remarks ? `(${crEntry.remarks})` : ''}</div>
            </td>
            <td className={crEntry.entryId === toFocus ? 'focusedTransaction' : null} >{CrLink}</td>
        </tr >
    })

    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" >{accountData.name}</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`/account/update/${props.match.params.id}`)}
                    > <i className="feather icon-edit text-c-black f-15 m-r-5" />Update</button>
                    <button
                        className="btn btn-danger btn-sm shadow-2 mb-4"
                        onClick={() => _triggerDeleteModal(accountData.id, props.match.params.id)}
                    > <i className="feather icon-trash-2 text-c-black f-15 m-r-5" />Delete</button>
                </div>
            </Row>
            <Row>
                <Col xl={12}>
                    <Card>
                        <Card.Body>
                            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <h5 className='mb-4 lh-1'>Account Details</h5>
                                {accountData.isClosed ?
                                    <Button variant='outline-primary' size='sm'
                                        className="mb-4"
                                        onClick={_triggerCloseModal}
                                    >Re-open Account</Button> :
                                    <Button variant='outline-primary' size='sm'
                                        className="mb-4"
                                        onClick={_triggerCloseModal}
                                    >Close Account</Button>
                                }
                            </div>
                            <div className="row d-flex align-items-center">

                                <Col md={1}></Col>
                                <Col md={10}>

                                    <div className="d-flex justify-content-between" >
                                        <div className="w-50">
                                            <div className='mb-1 border-bottom'><b>Account Name</b></div>
                                            <div className='mt-1 mb-3'>
                                                {accountData.name} {" "}
                                                {accountData.isClosed ? (<Badge variant="secondary">Closed</Badge>) : null}
                                            </div>
                                        </div>
                                        <div className="w-50 ml-4">
                                            <div className='mb-1 border-bottom'><b>Account Type</b></div>
                                            <div className='mt-1 mb-3'>{accountData.typeName}</div>
                                        </div>
                                    </div>

                                    <div className="d-flex justify-content-between" >
                                        <div className="w-50">
                                            <div className='mb-1 border-bottom'><b>Credit Amount</b></div>
                                            <div className='mt-1 mb-3'>Rs. {accountData.credit || 0}</div>
                                        </div>
                                        <div className="w-50 ml-4">
                                            <div className='mb-1 border-bottom'><b>Due Amount</b></div>
                                            <div className='mt-1 mb-3'>Rs. <Num>{accountData.due || 0}</Num></div>
                                        </div>
                                    </div>

                                    <div className="d-flex justify-content-between" >
                                        <div className="w-50">
                                            <div className='mb-1 border-bottom'><b>Balance</b></div>
                                            <div className='mt-1 mb-3'>Rs. <Num>{Math.abs(accountData.balance)}</Num></div>
                                        </div>
                                        <div className="w-50 ml-4">
                                            <div className='mb-1 border-bottom'><b>Opening Balance</b></div>
                                            <div className='mt-1 mb-3'>Rs. {accountData.openingBalance}</div>
                                        </div>
                                    </div>

                                    <div className='mb-1 border-bottom'><b>Opening Date</b></div>
                                    <div className='mt-1 mb-3'>{(new Date(accountData.openingDate)).toDateString()}</div>

                                    <div className='mb-1 border-bottom'><b>Parent Account</b></div>
                                    <div className='mt-1 mb-3'>{accountData.parentName || "Null"}</div>

                                    {accountData.customer || accountData.vendor ?
                                        <>
                                            <div className='mb-1 border-bottom'><b>Linked with</b></div>
                                            <div className='mt-1 mb-3'>
                                                {accountData.customer ? `${accountData.customer[0].name} [Customer]` : null}
                                                {accountData.vendor ? `${accountData.vendor[0].name} [Vendor]` : null}
                                            </div>
                                        </> :
                                        null
                                    }
                                </Col>
                                <Col md={1}></Col>



                            </div>
                        </Card.Body>
                        <Card.Body>
                            <div className="ledgerAccountTitle">
                                <div><b>Dr.</b></div>
                                <Card.Title style={{ textAlign: 'center', color: '#888888' }} as="h4"><b>{accountData.name} Account</b></Card.Title>
                                <div><b>Cr.</b></div>
                            </div>
                            <Table className="accountingReportTable" responsive>
                                <thead>
                                    <tr>
                                        <th>Date</th>
                                        <th>Particulars</th>
                                        <th>Amount</th>
                                        <th>Date</th>
                                        <th>Particulars</th>
                                        <th>Amount</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {EntriesList || null}
                                </tbody>
                                <tfoot>
                                    <tr>
                                        {accountData.balance >= 0 ?
                                            <>
                                                <td></td>
                                                <td></td>
                                                <td></td>
                                                <td></td>
                                                <td>By Balance c/d</td>
                                                <td><Num>{accountData.balance}</Num></td>
                                            </> :
                                            <>
                                                <td></td>
                                                <td>To Balance c/d</td>
                                                <td><Num>{accountData.balance * -1}</Num></td>
                                                <td></td>
                                                <td></td>
                                                <td></td>
                                            </>
                                        }

                                    </tr>
                                    <tr>
                                        <th>-</th>
                                        <th>Total</th>
                                        <th><Num>{drTotal > crTotal ? drTotal : crTotal}</Num></th>
                                        <th>-</th>
                                        <th>Total</th>
                                        <th><Num>{drTotal > crTotal ? drTotal : crTotal}</Num></th>
                                    </tr>
                                    <tr>
                                        {accountData.balance >= 0 ?
                                            <>
                                                <th>-</th>
                                                <th>Balance b/d</th>
                                                <th><Num>{accountData.balance}</Num></th>
                                                <th>-</th>
                                                <th>-</th>
                                                <th>-</th>
                                            </> :
                                            <>
                                                <th></th>
                                                <th></th>
                                                <th></th>
                                                <th></th>
                                                <th>Balance b/d</th>
                                                <th><Num>{accountData.balance * -1}</Num></th>
                                            </>
                                        }

                                    </tr>
                                </tfoot>
                            </Table>


                        </Card.Body>
                    </Card>
                </Col>
                <DeleteModal
                    show={deleteData.showModal}
                    onCancel={() => setDeleteData({ ...deleteData, showModal: false, accountId: null, accountUuid: null })}
                    onDelete={() => _delete(deleteData.accountId, deleteData.accountUuid)}
                />


                {/* Account Close Modal */}
                <Modal
                    style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
                    show={closeData.showModal}
                    onHide={() => setCloseData({ ...closeData, showModal: false })}
                >
                    <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                        <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Are you sure?</Modal.Title>
                    </Modal.Header>

                    <Modal.Body
                        style={{ paddingLeft: '30px' }}
                    >
                        You can undo this action later as well.
                    </Modal.Body>


                    <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                        <Button variant="secondary" onClick={() => setCloseData({ ...closeData, showModal: false })}>
                            Cancel
                        </Button>
                        <Button variant="danger" onClick={_closeAccount}>
                            {accountData.isClosed ? 'Re-open' : 'Close'} Account
                        </Button>
                    </div>
                </Modal>

            </Row>
        </>
    );
}

export default Account;
