import { baseRequest } from '../base';
// api for Invoices 

const getLedgerEntries = async (data, signal) => {
    try {
        return await baseRequest('api/v1/accounting/ledger/entries/get', 'POST', data, signal);
    } catch (e) {
        alert(e)
    }
};




