import React from 'react';

const Num = (props) => {
    let newNum = props.children.toLocaleString()



    if (newNum % 1 !== 0 && typeof (newNum) === 'number') {
        newNum = props.children.toFixed(2)
    }

    newNum = newNum.toLocaleString();

    return (
        <span>{newNum}</span>
    )
}

export default Num;