import { baseRequest } from '../base';
// api for vendors

const getVendors = async (data, signal) => {
  try {
    return await baseRequest('api/v1/inventory/vendors/get','POST', data, signal);
  } catch(e){
    alert(e)
  }
};

const createVendor = async (data, signal) => {
    try {
      return await baseRequest('api/v1/inventory/vendor/add','POST', data, signal);
    } catch(e){
      alert(e)
    }
};


const deleteVendors = async (data, signal) => {
  try {
    return await baseRequest('api/v1/inventory/vendors/delete','POST', data, signal);
  } catch(e){
    alert(e)
  }
};


const updateVendor = async (data, signal) => {
  try {
    return await baseRequest('api/v1/inventory/vendor/update','POST', data, signal);
  } catch(e){
    alert(e)
  }
};

const getVendor = async (data, signal) => {
    try {
      return await baseRequest('api/v1/inventory/vendor/get','POST', data, signal);
    } catch(e){
      alert(e)
    }
  };

export { getVendors, createVendor, deleteVendors, updateVendor, getVendor}