import React from 'react';

import { Modal, Button } from 'react-bootstrap';

const DeleteModal = (props) => {


    return (
        <Modal
            style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
            show={props.show}
            onHide={props.onCancel}
        >
            <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Are you sure?</Modal.Title>
            </Modal.Header>

            <Modal.Body
                style={{ paddingLeft: '30px' }}
            >
                {props.message ? props.message : 'This action cannot be undone.'}
            </Modal.Body>


            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                <Button variant="secondary" onClick={props.onCancel}>
                    <b>Cancel</b>
                </Button>
                {props.noRedButton ?
                    null :
                    <Button variant="danger" onClick={props.onDelete}>
                        <b>{props.redTitle || 'Delete'}</b>
                    </Button>
                }
            </div>
        </Modal >
    )

}

export default DeleteModal;